import React from 'react'

export const Todo = ({ text, todo, todos, setTodos }) => {
    const deleteHandler = () => {
        setTodos(todos.filter(el => el.id !== todo.id));
    }
    const completeHandler = () => {
        setTodos(
            todos.map((item) => {
            if (item.id === todo.id){
                return {
                    ...item,
                    completed: !item.completed,
                };
            }
            return item;
        }))
    }

    const editHandler = () => {

        // setTodos(
        //     todos.map((item) => {
        //         if(todo.item === item){
        //             console.log(todo.text);
        //             todo.text = text;   
        //             console.log(todo.text);
        //           };
        //           return todo;
        // }))
    }

    return (
        <div className="todo">
            <li className={`todo-item ${todo.completed ? "completed" : "" }`}>{text}</li>
            <button onClick={editHandler} className="edit-btn"><i className="fa fa-pencil"></i></button>
            <button onClick={completeHandler} className="complete-btn"><i className="fa fa-check"></i></button>
            <button onClick={deleteHandler} className="trash-btn"><i className="fa fa-trash"></i></button>
        </div>
    )
}
